import { useState, useEffect } from 'react'
import { useDispatch } from 'react-redux'
import styles from '../styles/modules/list.module.scss'

const List = ({ data, setData }) => {
  const dispatch = useDispatch()
  const [newData, setNewData] = useState('')

  useEffect(() => {
    const onKeypress = (evt) => {
      if (evt.key === 'Enter' && newData) {
        const newDataArray = [...data]
        newDataArray.push(newData)
        dispatch(setData(newDataArray))
        setNewData('')
      }

      if (!newData) {
        const newDataArray = data.filter((user) => user)
        dispatch(setData(newDataArray))
      }
    }

    document.addEventListener('keypress', onKeypress)

    return () => {
      document.removeEventListener('keypress', onKeypress)
    }
  })

  return (
    <section className={styles.list__wrap}>
      <ul className={styles.list}>
        {data.map((user, index) => {
          return (
            <li className={styles.item} key={index}>
              <input
                className={styles.user}
                type='text'
                value={user}
                onChange={(e) => {
                  const newDataArray = [...data]
                  newDataArray.splice(index, 1, String(e.target.value))
                  dispatch(setData(newDataArray))
                }}
                onBlur={() => {
                  const newDataArray = data.filter((user) => user)
                  dispatch(setData(newDataArray))
                }}
              />
            </li>
          )
        })}
        <li className={styles.item}>
          <input
            className={styles.new_user}
            autoFocus
            type='text'
            value={newData}
            onChange={(e) => {
              setNewData(String(e.target.value))
            }}
            onBlur={() => {
              if (newData) {
                const newDataArray = [...data]
                newDataArray.push(newData)
                dispatch(setData(newDataArray))
                setNewData('')
              }
            }}
          />
        </li>
      </ul>
    </section>
  )
}

export default List
