import { useEffect } from 'react'
import styles from '../styles/modules/main.module.scss'
import { useSelector } from 'react-redux'
import { setUsers, setQuestions } from '../reducer/reducer'
import List from './list'
import Timer from './timer'

const Main = () => {
  const {
    isShowMembers,
    isShowQuestions,
    currentUser,
    currentQuestion,
    timer,
    users,
    questions,
    isShowTimer,
  } = useSelector((state) => state)

  useEffect(() => {
    localStorage.setItem('users', JSON.stringify(users))
    localStorage.setItem('questions', JSON.stringify(questions))
  }, [users, questions])

  let time = new Date()
  time.setSeconds(time.getSeconds() + timer)

  return (
    <main className={styles.main}>
      <div className={styles.main__wrap}>
        <section className={styles.main__forms}>
          <div className={styles.main__users}>
            {isShowMembers ? (
              <h2 className={styles.main__title}>список участников</h2>
            ) : null}
            {isShowMembers ? <List data={users} setData={setUsers} /> : null}
          </div>
          <div className={styles.main__questions}>
            {isShowQuestions ? (
              <h2 className={styles.main__title_questions}>список вопросов</h2>
            ) : null}
            {isShowQuestions ? (
              <List data={questions} setData={setQuestions} />
            ) : null}
          </div>
        </section>
        <section className={styles.main__content}>
          <div
            className={styles.main__user}
            style={{
              backgroundColor: `${
                isShowMembers || isShowQuestions ? 'inherit' : ' #ffffff'
              }`,
            }}
          >
            {currentUser}
          </div>
          <div
            className={styles.main__question}
            style={{
              backgroundColor: `${
                isShowMembers || isShowQuestions ? 'inherit' : ' #ffffff'
              }`,
            }}
          >
            {currentQuestion}
          </div>
          {isShowTimer ? <Timer expiryTimestamp={time} /> : null}
        </section>
      </div>
    </main>
  )
}

export default Main
