import { useEffect } from 'react'
import { useTimer } from 'react-timer-hook'
import { useSelector } from 'react-redux'
import styles from '../styles/modules/timer.module.scss'
import start from '../img/start.svg'
import pauseI from '../img/pause.svg'

const Timer = ({ expiryTimestamp }) => {
  const { timer } = useSelector((state) => state)

  const {
    seconds,
    minutes,
    pause,
    resume,
    restart,
  } = useTimer({
    expiryTimestamp,
  })

  useEffect(() => {
    pause()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const isDisabled = timer === 0

  return (
    <div className={styles.timer}>
      <div className={styles.timer__time}>
        <span>{minutes < 10 ? `0${minutes}` : minutes}</span>:
        <span>{seconds < 10 ? `0${seconds}` : seconds}</span>
      </div>
      <div className={styles.timer__text}>мин:сек</div>
      <div className={styles.timer__button_wrap}>
        <button disabled={isDisabled} onClick={resume}>
          <img src={start} alt={'start'} />
        </button>
        <button disabled={isDisabled} onClick={pause}>
          <img src={pauseI} alt={'pauseI'} />
        </button>
      </div>
      <button
        className={styles.timer__button_restart}
        disabled={isDisabled}
        onClick={() => {
          const time = new Date()
          time.setSeconds(time.getSeconds() + timer)
          restart(time)
        }}
      >
        Запустить
      </button>
    </div>
  )
}

export default Timer
