import styles from '../styles/modules/header.module.scss'
import { useDispatch, useSelector } from 'react-redux'
import {
  setIsShowMembers,
  setIsShowQuestions,
  setIsShowTimer,
  setCurrentUser,
  setUsedUsers,
  setCurrentQuestion,
  setTimer,
  setUsedQuestions,
} from '../reducer/reducer'
import { getRandomInt } from '../utils/utils'
import { useEffect, useState } from 'react'
import user from '../img/user.svg'
import question from '../img/question.svg'
import random from '../img/random.svg'
import timer from '../img/timer.svg'
import reset from '../img/reset.svg'

const Header = () => {
  const dispatch = useDispatch()
  const {
    isShowMembers,
    isShowQuestions,
    isShowTimer,
    users,
    questions,
    usedUsers,
    usedQuestion,
  } = useSelector((state) => state)

  const [noUsedUsers, setNoUsedUsers] = useState([])
  const [noUsedQuestions, setNoUsedQuestions] = useState([])

  useEffect(() => {
    const uniqueUsers = users.filter((el) => !usedUsers.includes(el))
    setNoUsedUsers(uniqueUsers)
  }, [users, usedUsers])

  useEffect(() => {
    const uniqueQuestions = questions.filter((el) => !usedQuestion.includes(el))
    setNoUsedQuestions(uniqueQuestions)
  }, [questions, usedQuestion])

  return (
    <header className={styles.header}>
      <div className={styles.header__wrap}>
        <h1 className={styles.header__title}>рандомайзер</h1>
        <ul className={styles.header__list}>
          <li className={styles.header__item}>
            <button
              className={styles.header__button}
              onClick={() => {
                dispatch(setCurrentUser('Участник'))
                dispatch(setCurrentQuestion('Вопрос'))
                dispatch(setUsedUsers([]))
                dispatch(setUsedQuestions([]))
                dispatch(setTimer(0))
                dispatch(setIsShowTimer(false))
                localStorage.setItem('usedUsers', JSON.stringify([]))
                localStorage.setItem('usedQuestion', JSON.stringify([]))
                localStorage.setItem('currentUser', JSON.stringify('Участник'))
                localStorage.setItem('currentQuestion', JSON.stringify('Вопрос'))            
                localStorage.setItem('isShowTimer', JSON.stringify(false))
              }}
            >
              <img src={reset} alt={'reset'} />
              <span>Сбросить</span>
            </button>
          </li>
          <li className={styles.header__item}>
            <button
              className={styles.header__button}
              onClick={() => {
                dispatch(setIsShowMembers(!isShowMembers))
                localStorage.setItem(
                  'isShowMembers',
                  JSON.stringify(!isShowMembers)
                )
              }}
            >
              <img src={user} alt={'user'} />
              <span>Список участников</span>
            </button>
          </li>
          <li className={styles.header__item}>
            <button
              className={styles.header__button}
              onClick={() => {
                if (noUsedUsers.length === 0) {
                  return dispatch(
                    setCurrentUser('Уникальных участников больше нет!')
                  )
                }
                const newCurrentUser =
                  noUsedUsers[getRandomInt(0, noUsedUsers.length - 1)]
                const newUsedUsers = [...usedUsers]
                newUsedUsers.push(newCurrentUser)
                dispatch(setCurrentUser(newCurrentUser))
                dispatch(setUsedUsers(newUsedUsers))
                localStorage.setItem(
                  'currentUser',
                  JSON.stringify(newCurrentUser)
                )
                localStorage.setItem('usedUsers', JSON.stringify(newUsedUsers))
              }}
            >
              <img src={user} alt={'user'} />
              <span>Сгенерировать участника</span>
            </button>
          </li>
          <li className={styles.header__item}>
            <button
              className={styles.header__button}
              onClick={() => {
                dispatch(setIsShowQuestions(!isShowQuestions))
                localStorage.setItem(
                  'isShowQuestions',
                  JSON.stringify(!isShowQuestions)
                )
              }}
            >
              <img src={question} alt={'question'} />
              <span>Список вопросов</span>
            </button>
          </li>
          <li className={styles.header__item}>
            <button
              className={styles.header__button}
              onClick={() => {
                if (noUsedQuestions.length === 0) {
                  return dispatch(
                    setCurrentQuestion('Уникальных вопросов больше нет!')
                  )
                }
                const newCurrentQuestion = noUsedQuestions[0]
                const newUsedQuestions = [...usedQuestion]
                newUsedQuestions.push(newCurrentQuestion)
                dispatch(setCurrentQuestion(newCurrentQuestion))
                dispatch(setUsedQuestions(newUsedQuestions))
                localStorage.setItem(
                  'currentQuestion',
                  JSON.stringify(newCurrentQuestion)
                )
                localStorage.setItem(
                  'usedQuestion',
                  JSON.stringify(newUsedQuestions)
                )
              }}
            >
              <img src={question} alt={'question'} />
              <span>Сгенерировать вопрос</span>
            </button>
          </li>
          <li className={styles.header__item}>
            <button
              className={styles.header__button}
              onClick={() => {
                if (noUsedQuestions.length === 0) {
                  return dispatch(
                    setCurrentQuestion('Уникальных вопросов больше нет!')
                  )
                }
                const newCurrentQuestion =
                  noUsedQuestions[getRandomInt(0, noUsedQuestions.length - 1)]
                const newUsedQuestions = [...usedQuestion]
                newUsedQuestions.push(newCurrentQuestion)
                dispatch(setCurrentQuestion(newCurrentQuestion))
                dispatch(setUsedQuestions(newUsedQuestions))
                localStorage.setItem(
                  'currentQuestion',
                  JSON.stringify(newCurrentQuestion)
                )
                localStorage.setItem(
                  'usedQuestion',
                  JSON.stringify(newUsedQuestions)
                )
              }}
            >
              <img src={random} alt={'random'} />
              <span>Случайный вопрос</span>
            </button>
          </li>
          <li className={styles.header__item}>
            <button
              className={styles.header__button}
              onClick={() => {
                dispatch(setIsShowTimer(!isShowTimer))
                localStorage.setItem(
                  'isShowTimer',
                  JSON.stringify(!isShowTimer)
                )
              }}
            >
              <img src={timer} alt={'timer'} />
              <span>Таймер</span>
            </button>
          </li>
          <li className={styles.header__item}>
            {!isShowTimer ? null : (
              <input
                className={styles.header__time}
                type='time'
                onChange={(e) => {
                  const newTimer = e.target.value
                    .split(':')
                    .reduce((prev, current) => +prev * 60 + +current)
                  dispatch(setTimer(newTimer))
                }}
              />
            )}
          </li>
        </ul>
      </div>
    </header>
  )
}

export default Header
