const isShowMembers = localStorage.isShowMembers
  ? JSON.parse(localStorage.isShowMembers)
  : false

const isShowQuestions = localStorage.isShowQuestions
  ? JSON.parse(localStorage.isShowQuestions)
  : false

const isShowTimer = localStorage.isShowTimer
  ? JSON.parse(localStorage.isShowTimer)
  : false

const currentUser = localStorage.currentUser
  ? JSON.parse(localStorage.currentUser)
  : 'Участник'

const currentQuestion = localStorage.currentQuestion
  ? JSON.parse(localStorage.currentQuestion)
  : 'Вопрос'

const users = localStorage.users ? JSON.parse(localStorage.users) : []

const questions = localStorage.questions
  ? JSON.parse(localStorage.questions)
  : []

const usedUsers = localStorage.usedUsers
  ? JSON.parse(localStorage.usedUsers)
  : []

const usedQuestion = localStorage.usedQuestion
  ? JSON.parse(localStorage.usedQuestion)
  : []

const timer = 0

const initialState = {
  isShowMembers,
  isShowQuestions,
  isShowTimer,
  currentUser,
  currentQuestion,
  timer,
  users,
  questions,
  usedUsers,
  usedQuestion,
}

const ActionType = {
  SET_IS_SHOW_MEMBERS: 'SET_IS_SHOW_MEMBERS',
  SET_IS_SHOW_QUESTIONS: 'SET_IS_SHOW_QUESTIONS',
  SET_IS_SHOW_TIMER: 'SET_IS_SHOW_TIMER',
  SET_CURRENT_USER: 'SET_CURRENT_USER',
  SET_CURRENT_QUESTION: 'SET_CURRENT_QUESTION',
  SET_TIMER: 'SET_TIMER',
  SET_USERS: 'SET_USERS',
  SET_QUESTIONS: 'SET_QUESTIONS',
  SET_USED_USERS: 'SET_USED_USERS',
  SET_USED_QUESTION: 'SET_USED_QUESTION',
}

export const setIsShowMembers = (status) => {
  return {
    type: ActionType.SET_IS_SHOW_MEMBERS,
    payload: status,
  }
}

export const setIsShowQuestions = (status) => {
  return {
    type: ActionType.SET_IS_SHOW_QUESTIONS,
    payload: status,
  }
}

export const setIsShowTimer = (status) => {
  return {
    type: ActionType.SET_IS_SHOW_TIMER,
    payload: status,
  }
}

export const setCurrentUser = (status) => {
  return {
    type: ActionType.SET_CURRENT_USER,
    payload: status,
  }
}

export const setCurrentQuestion = (status) => {
  return {
    type: ActionType.SET_CURRENT_QUESTION,
    payload: status,
  }
}

export const setTimer = (status) => {
  return {
    type: ActionType.SET_TIMER,
    payload: status,
  }
}

export const setUsers = (status) => {
  return {
    type: ActionType.SET_USERS,
    payload: status,
  }
}

export const setQuestions = (status) => {
  return {
    type: ActionType.SET_QUESTIONS,
    payload: status,
  }
}

export const setUsedUsers = (status) => {
  return {
    type: ActionType.SET_USED_USERS,
    payload: status,
  }
}

export const setUsedQuestions = (status) => {
  return {
    type: ActionType.SET_USED_QUESTION,
    payload: status,
  }
}

export const reducer = (state = initialState, action) => {
  switch (action.type) {
    case ActionType.SET_IS_SHOW_MEMBERS: {
      return { ...state, isShowMembers: action.payload }
    }
    case ActionType.SET_IS_SHOW_QUESTIONS: {
      return { ...state, isShowQuestions: action.payload }
    }
    case ActionType.SET_IS_SHOW_TIMER: {
      return { ...state, isShowTimer: action.payload }
    }
    case ActionType.SET_CURRENT_USER: {
      return { ...state, currentUser: action.payload }
    }
    case ActionType.SET_CURRENT_QUESTION: {
      return { ...state, currentQuestion: action.payload }
    }
    case ActionType.SET_TIMER: {
      return { ...state, timer: action.payload }
    }
    case ActionType.SET_USERS: {
      return { ...state, users: action.payload }
    }
    case ActionType.SET_QUESTIONS: {
      return { ...state, questions: action.payload }
    }
    case ActionType.SET_USED_USERS: {
      return { ...state, usedUsers: action.payload }
    }
    case ActionType.SET_USED_QUESTION: {
      return { ...state, usedQuestion: action.payload }
    }
    default:
      return state
  }
}
